<template>
  <table
    v-if="house"
    class="bv-example-row"
    style="text-align: center; margin: auto"
  >
    <tr>
      <td>
        <h3>{{ house. }}</h3>
      </td>
    </tr>
    <tr style="text-align: left">
      <td>-  : {{ house. }}</td>
    </tr>
    <tr style="text-align: left">
      <td>-   : {{ house. }}</td>
    </tr>
    <tr style="text-align: left">
      <td>-  : {{ house. }}</td>
    </tr>
    <tr style="text-align: left">
      <td>-  : {{ house. }}</td>
    </tr>
    <tr style="text-align: left">
      <td>
        -  :
        {{ (parseInt(house..replace(",", "")) * 10000) | price }}
      </td>
    </tr>
  </table>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "HouseDetail",
  computed: {
    ...mapState(["house"]),
    // house() {
    //   return this.$store.state.house;
    // },
  },
  filters: {
    price(value) {
      if (!value) return value;
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
};
</script>

